.popup-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  padding: 16px;
}

.popup-content {
  width: 100%;
  max-width: 640px;
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(0,0,0,.25);
  overflow: hidden;
  animation: popupIn .18s ease-out;
}

@keyframes popupIn {
  from { transform: translateY(8px); opacity: .8; }
  to   { transform: translateY(0);   opacity: 1; }
}

.popup-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 16px;
  border-bottom: 1px solid #eee;
}

.popup-title {
  margin: 0;
  font-size: 20px;
  font-weight: 700;
}

.popup-close {
  border: 0;
  background: transparent;
  font-size: 24px;
  line-height: 1;
  cursor: pointer;
}

.popup-image {
  width: 100%;
  height: 220px;
  object-fit: cover;
  display: block;
}

.popup-body {
  padding: 16px;
}

.popup-desc {
  margin: 0 0 12px;
  line-height: 1.6;
  color: #333;
}

.popup-meta {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin-bottom: 12px;
}

.popup-chip {
  background: #f3f4f6;
  border: 1px solid #e5e7eb;
  padding: 4px 8px;
  border-radius: 999px;
  font-size: 12px;
}

.popup-tags {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.popup-tag {
  background: #eef2ff;
  color: #3730a3;
  padding: 4px 8px;
  border-radius: 8px;
  font-size: 12px;
}

.popup-actions {
  padding: 0 16px 16px;
}

.popup-link {
  display: inline-block;
  background: #111827;
  color: #fff;
  padding: 10px 14px;
  border-radius: 10px;
  text-decoration: none;
}

